import { createSlice } from '@reduxjs/toolkit';

const dataFileSlice = createSlice({
  name: 'dataFile',
  initialState: {
    filePath: null,
    fileFormat: null,
    tableExpr: null,
  },
  reducers: {
    setDataFile: (state, action) => {
      const { filePath, fileFormat } = action.payload;
      state.filePath = filePath;
      state.fileFormat = fileFormat;
      state.tableExpr = `read_${fileFormat}('${filePath}')`;
    },
  },
});

export const { setDataFile } = dataFileSlice.actions;
export default dataFileSlice.reducer;
