import { configureStore } from '@reduxjs/toolkit';
import settingsReducer from './settingsSlice';
import { rowDataSlice } from './dataSlice';
import { agColDefsSlice } from './agColDefsSlice';
import { columnPreferenceSlice } from './columnPreferenceSlice';
import { columnMetaSlice } from './dataSlice';
import sqlReducer from './sqlSlice';
import viewingReducer from './viewingSlice';
import dataSourceReducer from './dataFileSlice';
import { boxPlotSlice } from './boxPlotSlice';
import folderBrowserReducer from './folderBrowserSlice';
import { baselineColumnStatsSlice, filteredColumnStatsSlice, cardinalitySlice } from './dataSlice';
import { heatMapSlice } from './heatMapSlice';
import { histogramSlice } from './histogramSlice';
import { bubblePlotSlice } from './bubblePlotSlice';
import layoutReducer from './layoutSlice';
import { initSettingsSlice } from './dataSlice';
import derivedColumnsReducer from './derivedColumnsSlice';
import toastReducer from './toastSlice';

export const rootReducer = {
  viewing: viewingReducer,
  settings: settingsReducer,
  folderBrowser: folderBrowserReducer,
  dataFile: dataSourceReducer,
  sql: sqlReducer,
  columnMeta: columnMetaSlice.reducer,
  agColDefs: agColDefsSlice.reducer,
  columnPreference: columnPreferenceSlice.reducer,
  rowData: rowDataSlice.reducer,
  baselineColumnStats: baselineColumnStatsSlice.reducer,
  filteredColumnStats: filteredColumnStatsSlice.reducer,
  cardinality: cardinalitySlice.reducer,
  boxPlot: boxPlotSlice.reducer,
  heatMap: heatMapSlice.reducer,
  histogram: histogramSlice.reducer,
  bubblePlot: bubblePlotSlice.reducer,
  layout: layoutReducer,
  initSettings: initSettingsSlice.reducer,
  derivedColumns: derivedColumnsReducer,
  toast: toastReducer,
};

export const store = configureStore({
  reducer: rootReducer,
  middleware: (getDefaultMiddleware) =>
    getDefaultMiddleware({
      serializableCheck: false,
    }),
});

