import { Tab } from '@/constants';

export function buildTab({name, component, weight=50} : {name: Tab, component?: string, weight: number}) {

    return {
        type: 'tab',
        name, weight,
        id: `tab-${name}`,
        component: component || name
    };
}

export function buildTabSet({nameList=[], component='', weight=50} : {nameList: Tab[], component?: string, weight: number}) {
    return {
        type: 'tabset',
        weight,
        id: `tabset-${nameList.join('-')}`,
        children: nameList.map(name => buildTab({name, component, weight}))
    };
}


export const FLEX_LAYOUT_GLOBAL = {
    tabEnableFloat: false,
    tabEnableRename: false,
    tabSetHeaderHeight: 16,
    splitterSize: 4,
    keepComponentState: true,
    tabEnableClose: true,
    enableRenderOnDemand: false,
};
