import { createSlice } from '@reduxjs/toolkit';
import { buildTabSet } from '@/components/layout/utils';
import { TABS, LAYOUT_TWO_COLUMNS, LAYOUT_THREE_COLUMNS, LAYOUT_SIDE_BY_SIDE } from '@/constants';

const LAYOUT_PRESETS = {
    [LAYOUT_THREE_COLUMNS]: {
        layout: {
            type: 'row',
            children: [
              buildTabSet({nameList: [TABS.ColumnNavigation], weight: 15}),
              {
                type: 'row',
                weight: 60,
                children: [
                    buildTabSet({nameList: [
                      TABS.MainTable,
                      TABS.BubblePlot,
                      TABS.HeatMap,
                      TABS.Histogram,
                      TABS.BoxPlot,
                    ], weight: 50}),
                    buildTabSet({nameList: [
                      TABS.Gallery, TABS.DistributionSummary, TABS.FreeFormSQL, TABS.BrowseFolder
                    ], weight: 30})
                ]
            },
            buildTabSet({nameList: [TABS.RowDetails, TABS.ColumnFilters], weight: 25})
          ]
        }
    },
    [LAYOUT_TWO_COLUMNS]: {
        layout: {
            type: 'row',
            children: [
              buildTabSet({nameList: [TABS.ColumnNavigation, TABS.RowDetails, TABS.ColumnFilters], weight: 15}),
              {
                type: 'row',
                weight: 60,
                children: [
                    buildTabSet({nameList: [
                      TABS.MainTable,
                      TABS.Histogram, TABS.BubblePlot, TABS.BoxPlot, TABS.HeatMap,
                    ], weight: 50}),
                    buildTabSet({nameList: [
                      TABS.Gallery, TABS.DistributionSummary, TABS.FreeFormSQL, TABS.BrowseFolder
                    ], weight: 50})
                ]
            }

          ]
        }
    },
    [LAYOUT_SIDE_BY_SIDE]: {
        layout: {
            type: 'row',
            children: [
                buildTabSet({nameList: [TABS.Gallery, TABS.RowDetails, TABS.ColumnFilters], weight: 50}),
                buildTabSet({nameList: [TABS.Histogram, TABS.BubblePlot, TABS.BoxPlot, TABS.HeatMap], weight: 50}),
            ]
        }
    }

}

const layoutSlice = createSlice({
  name: 'layout',
  initialState: {
    current: null,
    presets: LAYOUT_PRESETS,
    target: null

  },
  reducers: {
    saveCurrentLayout: (state, action) => {
      state.current = action.payload;
    },
    setTargetLayout: (state, action) => {
      state.target = action.payload;
    }
  },
});





export const {
  saveCurrentLayout,
  setTargetLayout,
} = layoutSlice.actions;

export default layoutSlice.reducer;
